<template lang="html">
  <div class="pop-over is-shown" :style="position">
    <div class="pop-over-header">
      <span class="pop-over-header-title"></span>
      <a
      class="pop-over-header-close-btn"
      ><i class="close icon link icon"></i></a>
      <div class="ui fitted divider"></div>
    </div>
    <div class="pop-over-content">
      <div class="pop-over-list">
        <ul>
          <li><a href="javascript:;" style="text-decoration:line-through">...</a></li>
          <li><a href="javascript:;" style="text-decoration:line-through">...</a></li>
          <li><a href="javascript:;" style="text-decoration:line-through">...</a></li>
          <li><a href="javascript:;" style="text-decoration:line-through"></a></li>
        </ul>
      </div>
      <div class="ui fitted divider"></div>
      <div class="pop-over-list">
        <ul>
          <li><a href="javascript:;" style="text-decoration:line-through">...</a></li>
          <li><a href="javascript:;" @click = "recoverItem">...</a></li>
        </ul>
      </div>
      <div class="ui fitted divider"></div>
      <div class="pop-over-list">
        <ul>
          <li><a href="javascript:;" @click = "recoverList"></a></li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
import Vue from 'vue'
  export default {
    name: 'pop-over',
    props: {
      databus: Object
    },
    data () {
      return {
        index: 0,
        maindata: [],
        listdata: {},
        position: {}
      }
    },
    methods: {
      recoverItem () {
        this.listdata.item = []
      },
      recoverList () {
        // 
        this.maindata.splice(this.index, 1)
        Vue.nextTick(() => {
          // 
          this.$emit('clearlist')
        })
      }
    },
    created () {
      this.databus.$on('dataToOpt', (index, maindata, listdata, position) => {
        this.index = index
        this.maindata = maindata
        this.listdata = listdata
        this.position = position
      })
    }
  }
</script>

<style>
  @import '../assets/style/reset.css';
  /*  */
  .pop-over {
    display: none;
    position: absolute;
    top: 50px;
    width: 260px;
    height: auto;
    overflow: hidden;
    background-color: #fff;
    border-radius: 3px;
    border: 1px solid #D6DADC;
    box-shadow: 0 1px 6px rgba(0, 0, 0, .15);
    z-index: 70;
  }
  /*  */
  .pop-over.is-shown {
    display: block;
  }
  /*  */
  .pop-over .divider {
    position: relative;
  }
  /*  */
  .pop-over-header {
    text-align: center;
  }
  
  .pop-over-header-title {
    display: block;
    width: 100%;
    height: 36px;
    color: rgba(140, 140, 140, 1.0);
    font-size: 14px;
    text-align: center;
    line-height: 36px;
    z-index: 10;
  }
  /*  */
  .pop-over-header-close-btn {
    position: absolute;
    top: 0;
    right: 0;
    padding: 8px;
    color: rgba(140, 140, 140, 1.0);
    cursor: pointer;
  }
  .pop-over-header-close-btn:hover {
    color: rgba(77, 77, 77, 1.0);
  }
  /*  */
  .pop-over-content {
    padding: 6px;
  }
  
  .pop-over-list {
    margin: 5px 0;
  }
  .pop-over-list ul {
    width: 100%;
    height: auto;
  }
  .pop-over-list ul li {
    display: list-item;
    width: 100%;
    height: 30px;
    line-height: 30px;
  }
  
  .pop-over-list ul li a{
    display: block;
    position: relative;
    width: 310px;
    margin-left: -10px;
    padding-left: 10px;
    color: rgba(140, 140, 140, 1.0);
    font-weight: 700;
    text-decoration: none;
  }
  .pop-over-list ul li a:hover {
    color: #fff;
    background-color: #0079BF;
  }
</style>



